import { useEffect, useState } from "react";
import axios from "axios";
import { Card } from "../Styled";
import Loader from "../Loading";
import { useNavigate, useLocation, useParams, Link} from "react-router-dom";
import "./index.css";
function PassengerTrip() {
  const [trip, setTrip] = useState(null);
  const [isLoading, setLoading] = useState(true);
  const tripId = useParams();
  const navigate = useNavigate();
  const location = useLocation();
  useEffect(() => {
    getTrip();
  }, []);
  const getTrip = async () => {
    const URL = "http://localhost:8000/";
    console.log("popopo", tripId.tripId);
    try {
      axios.get(`${URL}app/trip/trip/${tripId.tripId}`).then(
        function (success) {
          if (success.status === 200) {
            console.log(success);
            setTrip(success.data.data);
            setLoading(false);
          }
        },
        function (reject) {}
      );
    } catch (error) {
      console.log(error);
    }
  };
  return (
    <div className="tripList">
        <Link to="/app/passenger/">Back</Link>
      {!isLoading && trip !== null && (
        <div>
          <h1>
            {trip[0].scheduleId.startLocation}
            {" -> "}
            {trip[0].scheduleId.endLocation}
          </h1>
        </div>
      )}
      {!isLoading && trip === null && <div>Faraghhhhhhghghghghghg</div>}
      {isLoading && <Loader />}
    </div>
  );
}

export default PassengerTrip;
